import { mapState, mapActions } from "vuex";
import vCell from "@/components/v-cell";
import vHeader from "@/components/v-header";
export default {
  data() {
    return {
      walletInfo: [],
      noWallet: false
    };
  },
  components: {
    "v-cell": vCell,
    vHeader
  },
  computed: {
    ...mapState(["token"])
  },
  created() {
    this.verToken();
  },
  methods: {
    ...mapActions(["atnToken"]),
    verToken(){
     if(!this.token){
      this.showTip("登录超时，请重新登录");
      this.$router.push({path: "/mine/login"});
     }
    },
    loginOut(){
      this.$axios
          .get(this.api.loginOut, {
            headers: {"Authorization": this.token }
          })
          .then(res => {
            const resData = res.data;
            if (resData.code !== 1) {
              this.showTip("退出登录失败");
              return;
            }
            this.atnToken('');
            localStorage.removeItem("openId");
            localStorage.removeItem("token");
            console.log(0);
            let _this = this;
            setTimeout(() => {
              _this.$router.push({path: "/mine/login"});
            }, 1000);
          })
          .catch(res => {
            this.showTip("退出登录失败");
            this.atnToken('');
            let _this = this;
            setTimeout(() => {
              _this.$router.push({path: "/mine/login"});
            }, 1000);
          });
    }
  }
};



